body {
  font-family: Arial, sans-serif;
  background: #f7f8fa;
}
.container {
  max-width: 600px;
  margin: 40px auto;
  background: #fff;
  padding: 32px 24px;
  border-radius: 14px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
}
h2, h3 {
  text-align: center;
}
.form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.form label {
  font-weight: bold;
}
.form input, .form select {
  padding: 8px;
  border-radius: 6px;
  border: 1px solid #ccc;
  margin-top: 4px;
  transition: border 0.2s, box-shadow 0.2s;
}
.form input:focus, .form select:focus {
  border: 1.5px solid #007bff;
  outline: none;
  box-shadow: 0 0 0 2px #e7f1ff;
}
.checkbox-group {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 6px;
}
.months-select {
  margin-left: 16px;
  margin-bottom: 10px;
}
button[type="submit"] {
  padding: 10px 0;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 6px;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s;
  margin-top: 8px;
}
button[type="submit"]:focus {
  box-shadow: 0 0 0 2px #e7f1ff;
}
button[type="submit"]:hover {
  background: #0056b3;
}
.success {
  color: green;
  margin-top: 8px;
}

/* Form ve tablo arası boşluk */
.container + .decl-list-container {
  margin-top: 36px;
}

@media (max-width: 700px) {
  .container, .decl-list-container {
    max-width: 98vw;
    padding: 12px 2vw;
  }
  .decl-table th, .decl-table td {
    font-size: 0.98em;
    padding: 5px 2px;
  }
}
.error {
  color: red;
  margin-top: 8px;
}
ul {
  margin-top: 16px;
  padding-left: 20px;
}
